import {
  Box,
  TextField,
  Button,
  Typography,
  List,
  ListItem,
} from "@mui/material";
import { useState } from "react";

export const AdvancedFilter = (props) => {
  let [filter, setFilter] = useState(props.filter["_advanced"]);

  return (
    <Box
      sx={{
        padding: 5,
        width: "100%",
        overflow: "hidden",
        ".MuiTextField-root, .MuiButton-root": {
          margin: "15px 0",
        },
        ".MuiListItem-root": {
          lineHeight: "1em"
        }
      }}
    >
      <TextField
        multiline
        sx={{
          width: "100%",
        }}
        onChange={(i) => setFilter(i.target.value)}
        value={filter}
      />
      <Button
        variant="contained"
        sx={{
          margin: "0 auto",
        }}
        onClick={() => {
          props.onFilter(filter);
          props.onClose();
        }}
      >
        Apply
      </Button>
      <Typography
        sx={{
          lineHeight: "1.3em",
        }}
      >
        Logical operators:
        <List>
          <ListItem>= for total equivalnce</ListItem>
          <ListItem>~ for similarity</ListItem>
        </List>
        Conjunction operators:
        <List>
          <ListItem>&amp; for intersection of results</ListItem>
          <ListItem>+ for union of results</ListItem>
        </List>
        for example: <br />
        <Typography
          sx={{
            m: 1,
            p: 1,
            background: "#ccc",
            display: "block",
            fontFamily: "monospace",
          }}
        >
          day = Sunday &amp; place ~ CIS + dept = CS
        </Typography>
        gives all results at sunday and any CIS lab, in addition to all CS
        schedule. This input must be empty in order to use basic filter.
      </Typography>
    </Box>
  );
};
