
function matchAdvanced(data, pattern) {
    pattern = pattern.replace(/\s/g, "");
    let unions = pattern.split("+");
    let results = [];
    for (let union of unions) {
      let intersections = union.split("&");
      for (let i = 0; i < data.length; i++) {
        let item = { ...data[i] };
        let valid = true;
        for (let inter of intersections) {
          let op = "=";
          if (inter.search("~") !== -1) op = "~";
          
          let [key, value] = inter.split(op);
          if (typeof item[key] !== "string") {
            item[key] = item[key].toString();
          }
  
          if (op == "~") {
            valid &= item[key].toLowerCase().search(value.toLowerCase()) !== -1;
          } else {
            valid &= item[key].toLowerCase() == value.toLowerCase();
          }
        }
        if (valid) results.push(i);
      }
    }
  
    return results;
  }
  
  function matches(item, filter) {
    for (let key of Object.keys(filter)) {
      if (filter[key] && filter[key].length === 0) continue;
      if (key === "_advanced") continue;
  
      let value = item[key];
      if (value.constructor.name === 'Array') {
        let res = value.filter(i => filter[key].includes(i))
        if (res.length === 0)
          return false
      } 
      else if (!filter[key].includes(value)) {
        return false;
      }
    }
  
    return true;
  }

  function isIntersecting(arrA, arrB) {
    return arrA.filter((item1) => arrB.includes(item1)).length !== 0;
  }
  


  export function isMatching(item, filter, logger) {
    for (let key of Object.keys(filter)) {
      let mFilter = filter[key], mItem = item[key];

      if (item[key] == undefined || 
        filter[key] == undefined || filter[key].length === 0 || item[key].length === 0) continue;

      // Array-ify primitive values
      if (filter[key].join === undefined) mFilter = [filter[key]];
      if (item[key].join === undefined) mItem = [item[key]];

      if (!isIntersecting(mFilter, mItem)) {
        return false;
      }
    }
    return true;
  }
  
export function getIndices(data, filter) {
    let indices = [];
    if (filter["_advanced"]) {
      indices = matchAdvanced(data, filter["_advanced"]);
    } else {
      for (let i = 0; i < data.length; i++) {
        if (matches(data[i], filter)) {
          indices.push(i);
        }
      }
    }
  
    return indices
  }

