import Table from "@mui/material/Table";
import TableHead from "@mui/material/TableHead";
import TableCell from "@mui/material/TableCell";
import TableRow from "@mui/material/TableRow";
import TableBody from "@mui/material/TableBody";
import Day from "./Day";
// import data from "../extra/data/data";
// import cs from '../cs.json';
// import sc from '../sc.json';
import data from "../ready_data.json";

import { tableslots as slots } from "../times.js";
import { getIndices, dayIndices } from "./helpers";

function TableView(props) {
  // let data = cs.concat(sc);
  let indices = getIndices(data, props.filter)
  
  let days = {};
  for (let i of dayIndices) days[i] = [];
  for (let ind of indices) {
    days[data[ind].day].push(data[ind]);
  }

  let classNames = {
    0: "day-head",
    1: "place-head",
  }

  let heads = slots.map((slot, i) => (
    <TableCell
      key={i}
      className={classNames[i] != undefined ? classNames[i] : ""}
      sx={{
        textAlign: "center",
        border: 1,
      }}
    >
      {slot}
    </TableCell>
  ));
  return (
    <Table sx={{ 
      textAlign: "center",
      ".MuiTableCell-root": {
        fontSize: "1.2em",
        width: "12.5%"
      }, ".day-head": {
        fontWeight: "bold",
       }
      }}>
      <TableHead>
        <TableRow>{heads}</TableRow>
      </TableHead>
      <TableBody>
        {dayIndices.map((day, count) => (
          <Day
            info={props.info}
            name={day}
            key={count++}
            data={days[day]}
            filter={props.filter}
          />
        ))}
      </TableBody>
    </Table>
  );
}

export default TableView;
