import React from "react";
import TableRow from "@mui/material/TableRow";
import TableCell from "@mui/material/TableCell";
import Row from "./Row";

function Place(props) {
  let header = [
    <TableCell key={0} className="day-head" rowSpan={props.daySpan}>
      {props.dayName}
    </TableCell>,
    <TableCell key={1} className="place-head">
      {props.name}
    </TableCell>,
  ];
  let className = "";
  if (!props["data-key"] == 0) header = header.splice(1);

  if (props.lastOne == props.length - 1) {
    className = "last-row";
  }
  if (props.empty) {
    return (
      <React.Fragment>
        <TableRow key={-1} className={"empty-day"}>
          <TableCell className="day-head">{props.dayName}</TableCell>
          {[0, 1, 2, 3, 4, 5, 6].map((i) => (
            <TableCell sx={{ border: 0 }}></TableCell>
          ))}
        </TableRow>
      </React.Fragment>
    );
  }
  let row = <Row info={props.info} data={props.data} filter={props.filter} />;

  return (
    <React.Fragment>
      <TableRow className={className}>
        {header}
        {row}
      </TableRow>
    </React.Fragment>
  );
}

export default Place;
