// let slotted = ["8AM", "10AM", "12PM", "2PM", "4PM", "6PM", "8PM"];
let slotted = [
  "9AM",
  "10:05AM",
  '11:10AM',
  '12:15PM',
  '1:20PM',
  '2:25PM',
  '3:30PM'
]

let tableslots = ["Day", "Place"]
let gridslots = [];
for (let i = 0; i < slotted.length - 1; i++) {
  tableslots.push(`${slotted[i]} - ${slotted[i + 1]}`);
  gridslots.push(`${slotted[i+1]} - ${slotted[i]}`);
}


export { tableslots, gridslots };
