import "./App.css";
import React, { useEffect, useState } from "react";
import { Button, Grid } from "@mui/material";
import DialogFilter from "./components/Filter/DialogFilter";
import MainTabs from "./components/MainTabs";
import TableView from "./components/TableView";
import GridView from "./components/GridView";

function App() {
  let [filter, setFilter] = useState({});
  let [dialogOpen, setDialogOpen] = useState(false);
  let [info, setInfo] = useState({});

  useEffect(() => {
    fetch("/schedule/info.json")
    //fetch("https://bassem-essam.github.io/schedule-api/info.json")
    .then(res => res.json())
    .then(i => {setInfo(i)})
  }, [])

  return (
    <>
      <Grid
        container
        spacing={0}
        direction="column"
        alignItems="center"
        justifyContent="center"
        sx={{ p: 5, borderBottom: "2px solid #111" }}
      >
        <Grid item xs={3}>
          <Button onClick={() => setDialogOpen(true)} variant="contained">
            Set Filter
          </Button>
        </Grid>
      </Grid>
      <DialogFilter
        filter={filter}
        open={dialogOpen}
        onClose={(f) => {
          setDialogOpen(false);
        }}
        onFilter={setFilter}
      />
      {!/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini|Mobi/i.test(
          navigator.userAgent
        ) ? (
          <TableView info={info} filter={filter} />
        ) : (
          <GridView info={info} filter={filter} />
        )}
      {/* <MainTabs info={info} filter={filter} onFilter={setFilter}/> */}
    </>
  );
}

export default App;
