import TableCell from "@mui/material/TableCell";
// import { matches } from './helpers'
import { isMatching } from './helpers'
// import { importance } from "./info";

function Row(props) {
  let data = props.data.sort(compareTimes);
  let importance = props.info.importance == null ? [] : props.info.importance;

  data = new Array(6).fill(0);
  for (let d of props.data) {
    data[d.time - 1] = d;
  }
  let slots = data.map((lect, index) => {
    if (lect == 0) return <TableCell key={index} className="empty"></TableCell>;

    let dept = lect.dept.join === undefined ? lect.dept : lect.dept.join("+");
    let content = [lect.subject];
    if (!props.filter.dept || props.filter.dept.length !== 1)
      content.push(dept);
    
    let whatIsIt = lect.whom == 0 ? "Lecture" : "sec " + lect.whom;
    // if (lect.whom != 0) content.push(lect.whom);
    content.push(whatIsIt)
    if (lect.who) content.push(lect.who);
    content = content.map((el, i) => <span key={i}>{el}</span>);

    for (let important of importance) {
      // if (isMatching(lect, important["filter"], lect.subject == "Logic programming" && lect.whom == 0))
      if (isMatching(lect, important["filter"]))
        content.push(
          <span key={-1} className={"important " + important["type"].toLowerCase()}>
            {important["type"]}
          </span>
        );
    }

    let classes = ["cell"];
    if (lect.whom == 0) {
      classes.push("lecture");
    }
    return (
      <TableCell key={index} className={classes.join(" ")}>
        {content}
      </TableCell>
    );
  });
  return slots;
}



function matches(item, filter) {
  for (let key of Object.keys(filter)) {
    if (filter[key] && filter[key].length === 0) continue;
    if (key === "_advanced") continue;

    let value = item[key];
    if (value.constructor.name === "Array") {
      let res = value.filter((i) => filter[key].includes(i));
      if (res.length === 0) return false;
    } else if (!filter[key].includes(value)) {
      return false;
    }
  }
}

function compareTimes(a, b) {
  return Number(a.time) - Number(b.time);
}

export default Row;
